@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Staatliches&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Work+Sans&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Goldman&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html {
  overflow-x: hidden;
  font-family: 'Roboto', sans-serif;
  scroll-behavior: smooth;
}

button,
a,
li,
h1,
h2,
img {
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#Container {
  width: 1050px;
  margin: 0 auto;
}

#SectionTitle {
  text-align: center;
  margin: 20px 0px 40px 0px;
}

#SectionTitle h2 {
  font-size: 2.5rem;
  background: linear-gradient(to left, #ff00d4, #2f00ff);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Raleway', sans-serif;
  margin-bottom: 10px;
  text-transform: capitalize;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

#SectionTitle p {
  color: #3f3f3f;
  font-family: 'Ubuntu', sans-serif;
  text-transform: capitalize;
  font-weight: 700;
  margin: 10px 0px;
}

#SectionTitle #Line {
  width: 50px;
  height: 6px;
  background: linear-gradient(to left, #ff00d4, #2f00ff);
  margin: auto;
  border-radius: 5px;
  margin: 15px auto;
}

nav {
  background: #b24040;
  z-index: 10;
  display: -ms-grid;
  display: grid;
  align-items: center;
  padding: 4px 0px;
}

nav #NavContentWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

nav #NavContentWrapper #NavTitle {
  font-family: 'Staatliches', cursive;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  letter-spacing: 5px;
  color: white;
  text-shadow: 1px 1px 1px black;
}

nav #NavContentWrapper #NavLinks {
  font-family: 'Poppins', sans-serif;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

nav #NavContentWrapper #NavLinks li {
  margin: 0px 20px;
  list-style: none;
  padding: 5px 0px;
}

nav #NavContentWrapper #NavLinks li a {
  text-decoration: none;
  letter-spacing: 2px;
  color: white;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

nav #NavContentWrapper #NavLinksMobile {
  display: none;
}

nav #NavContentWrapper #Burger {
  display: none;
}

nav .active {
  padding: 8px 0px;
  border-bottom: 2px solid white;
}

header {
  background: linear-gradient(to left, #00000079, #000000f5),
    url(/Simple-React-UI/static/media/HeaderBg.37faaedc.jpg);
  background-repeat: no-repeat;
  background-position: center;
  background-attachment: fixed;
  background-size: 100% 100%;
  height: 100vh;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

header #HeaderDesc {
  width: 550px;
}

header #HeaderDesc h1 {
  font-size: 2.8rem;
  margin-bottom: 20px;
  font-family: 'Raleway', sans-serif;
  background: linear-gradient(to left, #ffd000, #ff7b00);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

header #HeaderDesc p {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 1.2rem;
  color: #cecece;
  margin-bottom: 30px;
  text-transform: capitalize;
}

header #HeaderDesc p span {
  color: orange;
}

header #HeaderDesc a {
  text-decoration: none;
  color: white;
  padding: 10px 20px;
  font-size: 1.5rem;
  border: 2px solid white;
  border-radius: 40px;
  transition: 0.5s;
}

header #HeaderDesc a:hover {
  background: cyan;
}

#About {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

#About #AboutContentWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

#About #AboutContentWrapper #AboutDesc {
  width: 45%;
}

#About #AboutContentWrapper #AboutDesc h2 {
  text-transform: capitalize;
  background: linear-gradient(to left, #ff00bf, #6200ff);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
  font-family: 'Lato', sans-serif;
  margin-bottom: 15px;
  font-size: 2rem;
}

#About #AboutContentWrapper #AboutDesc p {
  font-family: 'Montserrat', sans-serif;
  line-height: 25px;
  padding: 5px 0px;
  text-align: justify;
  font-weight: 600;
}

#About #AboutContentWrapper #AboutImage {
  width: 50%;
}

#About #AboutContentWrapper #AboutImage img {
  width: 100%;
  animation: AboutImage 2s infinite ease alternate-reverse both;
}

@keyframes AboutImage {
  from {
    transform: translateY(20px);
  }
  to {
    transform: translateY(-20px);
  }
}

#Service {
  padding: 80px 0px 50px 0px;
}

#Service #ServiceContentWrapper {
  display: flex;
  justify-content: space-around;
  align-items: stretch;
  flex-wrap: wrap;
}

#Service #ServiceContentWrapper #SingleServices {
  width: 300px;
  text-align: center;
  margin: 20px;
  padding: 30px 0px;
  transition: 0.3s;
  border-radius: 5px;
  border: 0.01px solid #e9e9e9;
}

#Service #ServiceContentWrapper #SingleServices:hover {
  box-shadow: 1px 2px 20px black;
  border: none;
}

#Service #ServiceContentWrapper #SingleServices:hover img {
  transform: scale(1.2);
}

#Service #ServiceContentWrapper #SingleServices #ServiceLogo {
  width: 30%;
  margin: auto;
}

#Service #ServiceContentWrapper #SingleServices #ServiceLogo img {
  width: 100%;
  transition: 0.3s;
}

#Service #ServiceContentWrapper #SingleServices #ServiceDesc {
  width: 90%;
  margin: auto;
  margin: 20px auto 10px auto;
}

#Service #ServiceContentWrapper #SingleServices #ServiceDesc h2 {
  margin-bottom: 10px;
  font-family: 'Work Sans', sans-serif;
  background: linear-gradient(to left, #ffd900, #ff6600);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

#Service #ServiceContentWrapper #SingleServices #ServiceDesc p {
  font-family: 'Ubuntu', sans-serif;
  font-weight: 500;
  color: grey;
  width: 90%;
  margin: auto;
}

#Mode {
  position: fixed;
  bottom: 0;
  right: 0;
  margin: 30px 20px;
}

#Mode #ModesIcons {
  color: #ffa600;
  font-size: 3rem;
  transition: 0.3s;
}

#Mode #ModesIcons:hover {
  color: cyan;
}

#Team {
  padding: 100px 0px 50px 0px;
}

#Team #TeamContentWrapper {
  display: flex;
  justify-content: space-around;
  align-items: stretch;
  flex-wrap: wrap;
}

#Team #TeamContentWrapper #SingleTeam {
  width: 300px;
  border-radius: 5px;
  position: relative;
  margin: 20px 10px;
  transition: 0.3s;
}

#Team #TeamContentWrapper #SingleTeam:hover {
  box-shadow: 1px 2px 20px black;
}

#Team #TeamContentWrapper #SingleTeam:hover #TeamDesc {
  transform: scale(1);
}

#Team #TeamContentWrapper #SingleTeam:hover #TeamDesc p {
  transform: translateX(0);
}

#Team #TeamContentWrapper #SingleTeam img {
  width: 100%;
  border-radius: 5px;
  height: 300px;
}

#Team #TeamContentWrapper #SingleTeam #TeamDesc {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: #00000099;
  border-radius: 5px;
  color: white;
  transform-origin: right;
  transform: scale(0);
  transition: 0.5s;
}

#Team #TeamContentWrapper #SingleTeam #TeamDesc h2,
#Team #TeamContentWrapper #SingleTeam #TeamDesc p {
  width: 70%;
  text-align: center;
  margin: 5px 0px;
}

#Team #TeamContentWrapper #SingleTeam #TeamDesc h2 {
  font-size: 2rem;
  font-family: 'Work Sans', sans-serif;
  background: linear-gradient(to left, #ff00bf, #6200ff);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
}

#Team #TeamContentWrapper #SingleTeam #TeamDesc p {
  font-family: 'Ubuntu', sans-serif;
  text-transform: capitalize;
  transform: translateX(-1000%);
  transition: 0.8s;
  transition-delay: 0.6s;
}

#Project {
  padding: 100px 0px 50px 0px;
}

#Project #ProjectContentWrapper {
  display: flex;
  justify-content: space-around;
  align-items: streatch;
  flex-wrap: wrap;
}

#Project #ProjectContentWrapper #SingleProject {
  width: 325px;
  margin: 20px 10px;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  border: 0.1px solid #ddd;
  transition: 0.3s;
  box-sizing: border-box;
  overflow-x: hidden;
}

#Project #ProjectContentWrapper #SingleProject:hover {
  box-shadow: 1px 2px 20px black;
  border: none;
}

#Project #ProjectContentWrapper #SingleProject:hover img {
  transform: scale(1.1);
}

#Project #ProjectContentWrapper #SingleProject #PojectImage {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}

#Project #ProjectContentWrapper #SingleProject #PojectImage img {
  width: 100%;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
  transition: 0.3s;
}

#Project #ProjectContentWrapper #SingleProject #ProjectDesc {
  padding: 10px 15px;
  text-align: center;
}

#Project #ProjectContentWrapper #SingleProject #ProjectDesc h2 {
  font-family: 'Work Sans', sans-serif;
  background: linear-gradient(to left, #ffd900, #ff6600);
  -webkit-background-clip: text;
          background-clip: text;
  -webkit-text-fill-color: transparent;
  margin-bottom: 5px;
  font-size: 1.7rem;
}

#Project #ProjectContentWrapper #SingleProject #ProjectDesc p {
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 600;
}

#Project #ProjectContentWrapper #SingleProject #ProjectDesc a {
  display: inline-block;
  text-decoration: none;
  padding: 10px 20px;
  font-size: 1.2rem;
  border: 3px solid purple;
  margin: 10px 0px;
  font-family: 'Goldman', cursive;
  color: purple;
  transition: all 0.5s;
  border-radius: 2px;
}

#Project #ProjectContentWrapper #SingleProject #ProjectDesc a:hover {
  background: purple;
  color: white;
  border-color: purple;
}

#StartContentWrapper {
  height: 70vh;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  width: 70%;
  margin: 0 auto;
}

#StartContentWrapper #InputField {
  margin: 20px 0px;
  width: 100%;
}

#StartContentWrapper button {
  width: 100%;
  padding: 10px 0px;
  outline: none;
  background: linear-gradient(45deg, blue, red);
  color: white;
  border: none;
  border-radius: 2px;
  transition: 0.3s;
  font-size: 1.3rem;
  font-family: 'Source Sans Pro', sans-;
}

#StartContentWrapper button:hover {
  background: orange;
}

#Start {
  padding: 50px 0px;
}

#Start from {
  width: 100%;
}

@media screen and (max-width: 1024px) {
  #Container {
    width: 770px;
  }
  nav #NavLinks {
    width: 100%;
  }
  nav #NavTitle {
    margin: 10px 0px;
    width: 10%;
  }
  header #HeaderDesc {
    text-align: center;
    width: 70%;
    margin: auto;
  }
  header #HeaderDesc h1 {
    margin-bottom: 10px;
  }
  header #HeaderDesc p {
    margin-bottom: 40px;
  }
  #About #AboutContentWrapper #AboutDesc {
    width: 60%;
  }
  #About #AboutContentWrapper #AboutDesc p {
    font-weight: 900;
  }
  #About #AboutContentWrapper #AboutImage {
    width: 40%;
    margin: 20px 0px;
  }
}

@media screen and (max-width: 1100px) {
  #Container {
    width: 900px;
  }
}

@media screen and (max-width: 1100px) and (max-width: 950px) {
  #Container {
    width: 750px;
  }
}

@media screen and (max-width: 768px) {
  #Container {
    width: 90%;
  }
  a {
    cursor: default;
  }
  nav {
    padding: 10px 0px;
  }
  nav #NavTitle {
    margin: 0;
  }
  nav #NavContentWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  nav #NavContentWrapper #Burger {
    display: block;
  }
  nav #NavContentWrapper #Burger i {
    font-size: 2rem;
    color: aliceblue;
    transition: 0.5s;
  }
  nav #NavContentWrapper #Burger i:hover {
    color: white;
  }
  nav #NavContentWrapper #Burger:hover #NavLinksMobile {
    transform: scale(1);
  }
  nav #NavContentWrapper #Burger #NavLinksMobile {
    background-color: #b24040;
    display: block;
    height: 100vh;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-direction: column;
    position: fixed;
    top: 0;
    right: 0;
    width: 50%;
    transform: scale(0);
    transform-origin: right;
    transition: 0.5s;
    z-index: 6;
  }
  nav #NavContentWrapper #Burger #NavLinksMobile li {
    display: block;
  }
  nav #NavContentWrapper #Burger #NavLinksMobile li a {
    display: block;
    text-decoration: none;
    font-size: 1.5rem;
    color: azure;
  }
  nav #NavLinks {
    width: 30%;
    display: none;
  }
  nav #NavLinks li {
    display: none;
  }
  nav #NavLinks li a {
    display: none;
  }
  header #HeaderDesc {
    text-align: center;
    width: 90%;
    margin: auto;
  }
  header #HeaderDesc h1 {
    font-size: 2.5rem;
    margin-bottom: 15px;
  }
  header #HeaderDesc p {
    margin-bottom: 40px;
  }
  #About {
    height: auto;
    padding: 100px 0px 50px 0px;
  }
  #About #AboutContentWrapper {
    flex-direction: column-reverse;
  }
  #About #AboutContentWrapper #AboutDesc {
    text-align: center;
    width: 100%;
  }
  #About #AboutContentWrapper #AboutDesc p {
    text-align: center;
    font-weight: 900;
  }
  #About #AboutContentWrapper #AboutImage {
    width: 90%;
    margin: 20px 0px;
  }
  #Service #SingleServices {
    width: 90%;
  }
  #Team #SingleTeam {
    width: 90%;
  }
  #Team #SingleTeam img {
    height: auto;
  }
}

